.site-modal {
 padding-top: 50px;
  width: 1280px;

&__top {
  margin-bottom: 50px;

  .swiper-slide {
    display: flex;
justify-content: center;
  }

  .product-img {
    height: 418px;
  }
}

.product-img-sm {
  height: 126px;
}

.newmySwiper {
  padding: 20px 0;
background-color: var(--grey-light, #F6F6F6);

.swiper-wrapper {
  padding: 0 20px;
}
}
}

.site-modal {
  @include mobile {
    padding: 10px;
    width: 90%;
  }
}


.modal__close {
transition: fill .3s ease-in-out, background-color .3s ease-in-out;

&:focus {
  background-color: #EBD1FF;
  fill: #7033AC;
}


  @include hover {
    fill: #A65CF0;
  }

  &:active {
    fill: #7033AC;
  }
}

.overlay {
  position: fixed;
  inset: 0;
  z-index: 400;
  background-color: rgba(#000, 0.6);
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  &--active {
    opacity: 1;
    visibility: visible;
  }
}


.buyClick {
  width: 624px;
  padding: 30px;

  .form {
    margin-bottom: 12px;
  }

&__title {
margin-bottom: 8px;
  color: var(--black, #333);
font-size: 32px;
font-weight: 400;
line-height: 100%;
}

&__descr {
  max-width: 374px;
  margin-bottom: 30px;
  color: var(--black, #333);
  font-size: 14px;
  line-height: normal;
}
}


.thank {
  width: 624px;
  height: 421px;
  padding: 118px 75px;

  &__content {
    display: flex;
    flex-direction: column;
align-items: center;
    gap: 32px;
  }

  &__title {

    display: block;
    color: var(--black, #333);
font-size: 32px;
}
}
