.nav-additional {
  &__list {
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: 10px 44px;

    @include small-tablet {
      justify-content: flex-start;
    }

    @include mobile {
      display: none;
    }

    @include tablet {
      gap: 10px 10px;
    }
  }

  &__item {}

  &__link {
    padding: 2px 10px;
    color: var(--grey, #999);
    font-size: 14px;
    line-height: 100%;
    transition: color .3s ease-in-out,  background-color .3s ease-in-out;

    &:focus {
      background-color: var(--pink-violet, #EBD1FF);
      color: var(--primary-shade, #7033AC);
    }

    @include hover {
      color: var(--primary, #A65CF0);
      background-color: transparent;
    }

    &:active {
      color: var(--primary-shade, #7033AC);
    }
  }
}

.nav {
  &__list {
    justify-content: flex-end;
    gap: 10px 72px;

    @include desktop {
      gap: 10px 21px;
    }

  }

  &__item {}

  &__link {
    padding: 2px 10px;
    color: var(--black, #333);
    transition: color .3s ease-in-out,  background-color .3s ease-in-out;

    &:focus {
      background-color: var(--pink-violet, #EBD1FF);
      color: var(--black, #333);
    }

    @include hover {
      color: var(--primary, #A65CF0);
      background-color: transparent;
    }

    &:active {
      color: var(--primary-shade, #7033AC);
    }
  }
}
