.hero {
  padding-bottom: 67px;

  &__slider {
    margin-bottom: 31px;
  }

  &__slide {
    img {
      min-height: 400px;

      @include mobile {
        min-height: 210px;
      }
    }
  }

  &__content {
    padding-top: 80px;
    padding-bottom: 80px;
    position: absolute;
    top: 0;
    left: calc((100% - var(--container-width)) / 2);
    max-width: 551px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 49px;

    @include big-desktop {
      left: 0;
    }

    @include mobile {
      padding-top: 29px;
      left: 18px;
    }
  }

  &__title {
    color: var(--grey, #999);
    font-size: 48px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;

    @include mobile {
font-size: 20px;
max-width: 80%;
    }
  }

  &__btn {
    min-width: 143px;

    @include mobile {
      display: none;
    }


  }
}

.hero__btn-mob {
  display: none;
  @include mobile {
    display: block;
    width: 100%;
  }
}

.swiper-pagination-bullet-active {
  background: #333333;
}

.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
}

.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
bottom: 23px;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
margin: 0 8px;
}
