/* stylelint-disable */
@mixin big-desktop {
  @media (max-width: (1280px)) {
    @content;
  }
}

@mixin middle-desktop {
  @media (min-width: (1200px)) {
    @content;
  }
}


@mixin desktop {
  @media (max-width: (1200px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1023px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (767px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
