.useful {
  padding-bottom: var(--padding-section);

  &__container {
    position: relative;
  }

  &__btns {
    position: absolute;
    top: 0;
    right: 12px;
    display: flex;
    gap: 13px;

    @include desktop {
      right: 28px;
    }

    @include tablet {
      right: 64px;
    }
  }

  &__btn {
    min-width: 104px;
  }
}


.card-blog {
  border-radius: 10px;

  &__image {
    font-size: 0;
  }

  &__content {
    padding: 26px 30px 30px 30px;
    min-height: 190px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
    border-radius: 0px 0px 10px 10px;
    background: var(--grey-light, #F6F6F6);
  }

  &__descr {
    color: var(--black, #333);
    font-size: 24px;
    line-height: normal;

    @include desktop {
      font-size: 16px;
    }
  }

  &__btn {
    align-self: flex-start;
  }
}
