.search {

  &__wrap {
    position: relative;
    width: 100%;
  }

  &-ic{
          position: absolute;
      top: 50%;
      left: 32px;
      width: 20px;
      height: 20px;
      transform: translateY(-50%);
      fill: var(--grey, #999);
      transition: fill .3s ease-in-out;
  }

  &__input {
    padding: 18px 59px;
    width: 100%;
    min-height: 60px;
    border: 2px solid transparent;
    border-radius: 10px 0px 0px 10px;
    background-color: var(--grey-light, #F6F6F6);
    color: var(--black, #333);
    transition: background-color .3s ease-in-out, border .3s ease-in-out;

    &::placeholder {
      color: var(--grey, #999);
    }

    @include hover {
      background-color: var(--md-grey, #EAEAEA);
          }

    &:focus {
      outline: none;
      border: 2px solid var(--primary, #A65CF0);
      background-color: var(--pink-light, #F8EFFF);
    }
  }

  &__btn {
    position: absolute;
    content: "";
    top: 50%;
    right: 32px;
    transform: translateY(-50%);
  }

  &__select {
    min-width: 300px;
    min-height: 60px;

    @include tablet {
      min-width: 189px;
    }
  }
}

.not-focus {
  border: 2px solid transparent !important;
  background: var(--grey-light, #F6F6F6) !important;
}

.is-focus {
  fill: #A65CF0;
}
