.footer {
&__top {
  padding-top: 27px;
padding-bottom: 20px;
background: var(--md-grey, #EAEAEA);

@include tablet {
  padding-top: 21px;
}
}

&__container {
  display: flex;
  justify-content: space-between;

  @include tablet {
    flex-direction: column;
  }
}

&__left {
  display: flex;
  flex-direction: column;
  gap: 27px;

  @include tablet {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 25px;
  }

  @include small-tablet {
   flex-direction: column;
   align-items: center;
  }
}

&__social {
  display: flex;
  gap: 12px;
}

&__right {
  // max-width: 760px;
  // width: 100%;
}





  &__bottom {
    padding: 5px;
  }
}

.copyright {
  color: var(--grey, #999);
text-align: center;
font-size: 14px;
}

.social {

  &__item {
    font-size: 0;
  }

  &__link {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 36px;
    fill: var(--grey, #999);
    transition:  background-color .3s ease-in-out, fill .3s ease-in-out;

    svg {
      // display: inline-block;
      width: 30px;
      height: 30px;

    }

    &:focus {
      outline: none;
      background-color: var(--primary-light, #C998FB);
      fill: #fff;
    }

    @include hover {
fill: #A65CF0;
background-color: transparent;
    }

    &:active {
      fill: #7033AC;
    }
  }
}

.menu-list {
  display: flex;
  justify-content: space-between;
  column-gap: 139px;

  @include desktop {
    gap: 105px;
  }

  @include tablet {
    gap: 46px;
    justify-content: flex-start;
  }

  @include small-tablet {
    flex-wrap: wrap;
    gap: 24px 0;
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 10px;

    &:first-child {
      transform: translateX(27px);
    }

    @include tablet {
      &:first-child {
        transform: translateX(0);
      }

      &:last-child {
        transform: translateX(31px);
      }
    }

    @include small-tablet {
width: 50%;

      &:last-child {
        transform: translateX(0);
      }
    }
  }

  &__title {
    color: var(--black, #333);
line-height: 100%;
  }

  &__link {
    color: var(--grey, #999);
font-size: 14px;
line-height: 100%;
transition: color .3s ease-in-out,  background-color .3s ease-in-out;

&:focus {
  background-color: var(--pink-violet, #EBD1FF);
  color: var(--primary-shade, #7033AC);
}

@include hover {
  color: var(--primary, #A65CF0);
  background-color: transparent;
}

&:active {
  color: var(--primary-shade, #7033AC);
}


  }
}


