.card {
  position: relative;
  border-radius: 10px;
  background: var(--grey-light, #F6F6F6);

  &__image {
    font-size: 0;
    text-align: center;
  }

  &__title {
    max-width: 226px;
    color: var(--black, #333);
    font-size: 24px;
    font-weight: 400;
    line-height: 130%;

    @include mobile {
      font-size: 16px;
    }
  }

  &__info {
    padding: 0 5px 0 15px;
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include mobile {
      padding: 0;
      gap: 16px;
    }
  }

  &__btn {
    align-self: flex-start;

    @include mobile {
      align-self: center;
      min-width: 108px;
      // max-width: 108px;
      padding: 10px 10px;
    }
  }

  &__link {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    color: var(--primary, #A65CF0);
    font-weight: 700;
    line-height: 120%;
    transition: color .3s ease-in-out;

    &::after {
      position: absolute;
      content: '';
      bottom: -2px;
      left: 0;
      width: 80px;
      height: 2px;
      background-color: currentColor;
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    }


    &:focus {
      .btn-arrow-small {
        outline: none;
        border: 2px solid var(--primary-shade, #7033AC);
        background-color: var(--primary-light, #C998FB);
        color: #FFF;
      }

      &::after {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      &::after {
        opacity: 1;
        visibility: visible;
        animation: left .3s linear;
      }

      .btn-arrow-small {
        background-color: var(--primary-light, #A65CF0);
        border-color: var(--primary-light, #A65CF0);
        color: #FFF;
      }
    }

    @keyframes left {
      0% {
        width: 0;
      }

      100% {
        width: 80px;
      }
    }

    &:active {
      color: var(--primary-shade, #7033AC);

      &::after {
        opacity: 1;
        visibility: visible;
      }


      .btn-arrow-small {
        background-color: var(--primary-shade, #7033AC);
        border-color: var(--primary-shade, #7033AC);
        color: #FFF;
        ;
      }
    }
  }

  &__list {
    display: flex;
    gap: 30px;

    @include mobile {
      gap: 20px;
    }
  }

  &__item-link {
    color: var(--grey, #999);
    font-size: 14px;
    line-height: 100%;
  }
}

.discount {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 92px;
  height: 34px;
  color: var(--white, #FFF);
  font-size: 14px;
  font-weight: 700;
  line-height: normal;
  background-image: url('../img/discount-bg.svg');
}

.rating-product {
  padding-left: 22px;
  color: var(--secondary, #FF862F);
  line-height: 130%;
  background-image: url('../img/star.svg');
  background-repeat: no-repeat;
  background-position: left top 3px;
  background-size: 15px 15px;

  @include mobile {
    padding: 3px 3px 0 22px;
font-size: 12px;

  }
}

.prices {

  &__accent {
    color: var(--secondary, #FF862F);
    font-size: 18px;
    line-height: 130%;
  }

  &__old {
    color: var(--grey, #999);
    line-height: 130%;
    text-decoration: line-through;
  }


  &__current {
    color: var(--grey-shade, #666);
    line-height: 130%;
  }
}
