.product {

  &__slide {
    width: max-content !important;
  }

  &-img {
    object-fit: contain;
    height: 245px;

    &-sm {
      object-fit: contain;
      height: 61px;
    }
  }
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 32px;

  &__sw1 {
    grid-column: 6 span;
    height: 245px;
  }

  &__info {
    grid-column: 6 span;
    height: 261px;
  }

  &__sw2 {
    margin-bottom: 32px;
    padding: 10px;
    background-color: var(--grey-light, #F6F6F6);
    grid-column: 6 span;
    height: 81px;
  }

  &__test {
    grid-column: 6 span;
  }

  &__descr {
    grid-column: 6 span;
    background-image: url('../img/product/1-bg.png');
    background-repeat: no-repeat;
    background-position: bottom left;
  }

  &__text {
    grid-column: 6 span;
  }
}

.product-info {
  padding-top: 14px;

  &__rating {
    margin-bottom: 2px;
  }

  &__title {
    margin-bottom: 5px;
    color: var(--black, #333);
    font-size: 24px;
    font-weight: 400;
    line-height: 130%;
  }

  &__descr {
    display: block;
    margin-bottom: 21px;
    color: var(--grey, #999);
    font-size: 14px;
    line-height: 100%;
  }

  &__price {
    display: block;
    margin-bottom: 21px;
    color: var(--black, #333);
    font-size: 36px;
    line-height: 130%;
  }

  &__btns {
    display: inline-flex;
    flex-direction: column;
    gap: 12px;
  }

  &__btn {
    padding: 19px 35px;
  }


}

.product-descr {

  &__title {
    margin-bottom: 8px;
    color: var(--black, #333);
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
  }

  &__info {
    color: var(--black, #333);
    font-size: 14px;
    line-height: 180%;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}

.product-text {
  margin-bottom: 8px;
  display: flex;
  justify-content: space-between;

  &__item {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }


  &__wrap {
    width: 100%;
    display: flex;
    align-items: flex-end;
  }

  &__subtitle {
    flex-shrink: 0;
    color: var(--grey, #999);
    font-size: 14px;
  }

  &__dotted {
    margin: 0 2px 2px 2px;
    display: inline-flex;
    width: 100%;
    border-bottom: 1px dotted var(--grey, #999);
  }

  &__info {
    min-width: 216px;
    color: var(--black, #333);
    font-size: 14px;
  }



}
