.btn {
  &-secondary {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    min-width: 128px;
    padding: 17px 10px;
    border-radius: 10px;
    border: 2px solid var(--primary, #A65CF0);
    color: var(--primary, #A65CF0);
    font-weight: 600;
    line-height: 100%;
    transition: color .3s ease-in-out, background-color .3s ease-in-out, border .3s ease-in-out;

    &:focus {
      outline: none;
      color: var(--white, #FFF);
      background-color: var(--primary-light, #C998FB);
    }

    @include hover {
      color: var(--white, #FFF);
      background: var(--primary, #A65CF0);
    }

    &:active {
      color: var(--white, #FFF);
      border: 2px solid var(--primary, #7033AC);
      background-color: var(--primary-shade, #7033AC);
    }

  }

  &-primary {
    display: flex;
    padding: 20px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    border-radius: 10px;
    background-color: var(--primary, #A65CF0);
    color: var(--white, #FFF);
    font-weight: 700;
    line-height: 100%;
    border: 2px solid transparent;
    transition: background-color .3s ease-in-out, border .3s ease-in-out;

    &:focus {
      outline: none;
      border: 2px solid var(--primary-shade, #7033AC);
      background-color: var(--primary-light, #C998FB);
    }

    @include hover {
      background-color: var(--primary-light, #C998FB);
    }

    &:active {
      background-color: var(--primary-shade, #7033AC);
    }
  }

  &-arrow {
    position: relative;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    border: 2px solid #A65CF0;
    color: #A65CF0;
    transition: background-color .3s ease-in-out, border .3s ease-in-out, color .3s ease-in-out;

    &::before {
      position: absolute;
      content: '';
      width: 8px;
      height: 8px;
      border-top: 2px solid currentColor;
      border-right: 2px solid currentColor;
    }

    &-next {
      &::before {
        transform: rotate(45deg);

      }
    }

    &-prev {
      &::before {
        transform: rotate(-135deg);

      }
    }

    &:focus {
      outline: none;
      border: 2px solid var(--primary-shade, #7033AC);
      background-color: var(--primary-light, #C998FB);
      color: #FFF;
    }

    @include hover {
      background-color: var(--primary-light, #A65CF0);
      border-color: var(--primary-light, #A65CF0);
      color: #FFF;
    }

    &:active {
      background-color: var(--primary-shade, #7033AC);
      border-color: var(--primary-shade, #7033AC);
      color: #FFF;
    }
  }

  &-arrow-small {
    position: relative;
    width: 22px;
    height: 22px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    border: 2px solid #A65CF0;
    color: #A65CF0;
    transition: background-color .3s ease-in-out, border .3s ease-in-out, color .3s ease-in-out;


    &::before {
      position: absolute;
      content: '';
      width: 6px;
      height: 6px;
      border-top: 2px solid currentColor;
      border-right: 2px solid currentColor;
    }

    &--next {
      &::before {
        transform: rotate(45deg);

      }
    }

    &-prev {
      &::before {
        transform: rotate(-135deg);

      }
    }
  }

  &-add {
    padding: 2px;
    color: var(--primary, #A65CF0);
    line-height: 130%;
    border: 1px solid transparent;
    transition: color .3s ease-in-out, border .3s ease-in-out;

    &:focus {
      border: 1px solid var(--primary-light, #C998FB);
    }

    @include hover {
      color: var(--primary-light, #C998FB);
    }

    &:active {
      color: var(--primary-shade, #7033AC);
      ;

    }


  }
}

.swiper-button-disabled {
  background-color: #FFF !important;
  border-color: var(--grey, #999) !important;
  color: var(--grey, #999) !important;
}
