.form {
  display: flex;
  flex-direction: column;
  gap: 30px;

  &__input {
    outline: none;
    width: 100%;
    height: 54px;
    padding: 5px 18px;
    border-radius: 10px;
    border: 2px solid transparent;
    background-color: var(--grey-light, #F6F6F6);
    transition: background-color .3s ease-in-out, border .3s ease-in-out, outline .3s ease-in-out;

    @include hover {
      background-color: var(--md-grey, #EAEAEA);
    }

    &:focus {
      outline: none;
      border: 1px solid var(--primary, #A65CF0);
      background-color: var(--pink-light, #F8EFFF);
    }


    &:active {
      border: 1px solid var(--green-input, #B8EC64);
      background-color: var(--grey-light, #F6F6F6);
    }
  }


  &__btn {
    max-width: 159px;
    width: 100%;
  }
}

.input--active {
  border: 1px solid var(--green-input, #B8EC64) !important;
  background-color: var(--grey-light, #F6F6F6) !important;
}
