.cabinet {

  &__btn {
    padding: 2px;
    transition: fill .3s ease-in-out,  background-color .3s ease-in-out;

    &:focus {
      fill: #fff;
      background-color:  var(--primary-light, #C998FB);
    }

    @include hover {
      fill: #C998FB;
      background-color: transparent;
    }

    &:active {
    fill: #7033AC;
    }

    &--user {
      width: 34px;
      height: 36px;

      svg {
        width: 26px;
        height: 28px;
        object-fit: cover;
      }
    }

    &--cart {
      position: relative;
      width: 49px;
      height: 34px;

      svg {
        position: absolute;
        top: 50%;
        right: 2px;
        width: 30px;
        height: 28px;
        object-fit: cover;
        transform: translateY(-50%);
      }
    }


  }

  &__current {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 4px;
    width: 14px;
    height: 15px;
    border-radius: 100px;
    font-size: 10px;
    font-weight: 700;
    color: var(--white, #FFF);
    background-color: var(--secondary, #FF862F);;
    transform: translateY(-50%);
  }
}
