.banner {
  background-image: url('../img/banner-bg.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 600px;

  @include mobile {
    min-height: 210px;
  }

  &__container {

    @include tablet {
      padding-left: 27px;
    }

    @include mobile {

    }
  }

  &__content {
    max-width: 508px;
    padding-top: 150px;

    @include mobile {
      max-width: 190px;
      padding-top: 30px;
    }
  }

  &__title {
    color: var(--gold, #D5B986);
    font-family: 'PlayfairDisplayBlack';
    font-size: 60px;
    font-weight: 900;
    line-height: 120%;

    @include mobile {
font-size: 24px;
    }
  }

  &__descr {
    margin-bottom: 49px;
    color: var(--white, #FFF);
    font-size: 48px;
    line-height: 120%;

    @include mobile {
      font-size: 16px;
          }
  }

  &__btn {
    min-width: 187px;

    @include mobile {
      display: none;
    }
  }
}

.banner__btn-mob {
  margin-top: 32px;
  display: none;
  @include mobile {
    display: block;
    width: 100%;
  }
}


.banner-sm {
  min-height: 360px;
  border-radius: 10px;
  background: var(--black, #333);

  &__img {
    font-size: 0;
  }

  &__content {
    padding: 30px;
  }

  &__title {
    color: var(--gold, #D5B986);
    font-family: Playfair Display;
    font-size: 24px;
    font-weight: 900;
    line-height: normal;
  }

  &__descr {
    margin-bottom: 12px;
    width: 90%;
    color: var(--grey, #999);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__link {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    color: var(--primary, #A65CF0);
    font-weight: 700;
    line-height: 120%;
    transition: color .3s ease-in-out;

    &::after {
      position: absolute;
      content: '';
      bottom: -2px;
      left: 0;
      width: 80px;
      height: 2px;
      background-color: currentColor;
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    }


    &:focus {
      .btn-arrow-small {
        outline: none;
        border: 2px solid var(--primary-shade, #7033AC);
        background-color: var(--primary-light, #C998FB);
        color: #FFF;
      }

      &::after {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      &::after {
        opacity: 1;
        visibility: visible;
        animation: left .3s linear;
      }

      .btn-arrow-small {
        background-color: var(--primary-light, #A65CF0);
        border-color: var(--primary-light, #A65CF0);
        color: #FFF;
      }
    }

    @keyframes left {
      0% {
        width: 0;
      }

      100% {
        width: 80px;
      }
    }

    &:active {
      color: var(--primary-shade, #7033AC);

      &::after {
        opacity: 1;
        visibility: visible;
      }


      .btn-arrow-small {
        background-color: var(--primary-shade, #7033AC);
        border-color: var(--primary-shade, #7033AC);
        color: #FFF;
        ;
      }
    }
  }
}
