.rating {
  &__container {
    padding-bottom: var(--padding-section);
  }

  &__content {
    margin-bottom: 32px;

    @include mobile {
      gap: 16px;
    }
  }

  &__card {
    grid-column: 3 span;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 16px;
    padding: 30px 15px;
    min-height: 420px;
    // flex-shrink: 0;

    &--vis {
      &:nth-child(n + 9) {
        display: none;
      }

      @include desktop {
        &:nth-child(n + 7) {
          display: none;
        }
      }
    }


    @include mobile {
      padding: 15px;
      min-height: 278px;
    }
  }

  &__product {
    position: absolute;
    top: 30px;
    left: 30px;

    @include mobile {
      top: 15px;
      left: 15px;
    }
  }

  &__title {
    max-width: 245px;
  }

  &__btn {
    margin: 0 auto;
    min-width: 296px;
  }
}

.rating .is-visible {
  display: flex;
}
