.feedbabck {
  padding-bottom: var(--padding-section);

  &__container {
    display: flex;
    gap: 32px;
  }

  &__left {
    width: calc(50% - 16px);

    @include desktop {
      width: calc(44% - 16px);
    }

    @include tablet {
      width: calc(118% - 16px);
    }
  }

  &__title {
    margin-bottom: 9px;
    color: var(--black, #333);
    font-size: 32px;
    font-weight: 400;
    line-height: 100%;

    @include tablet {
      margin-bottom: 13px;
    }
  }

  &__descr {
    margin-bottom: 16px;
    color: var(--grey-shade, #666);
    font-size: 14px;
    line-height: normal;
  }

  &__form {
    margin-bottom: 12px;
  }

  &__right {
    padding-top: 25px;
    width: calc(50% - 16px);

    @include desktop {
      padding-top: 79px;
      width: calc(56% - 16px);
    }

    @include tablet {
      padding-top: 16px;
    }
  }

  &__btn {
    padding: 17px 20px 17px 20px;
  }

  &__image {
    font-size: 0;
  }

}

.feedbabck-checbox {
  display: flex;
  align-items: center;

  &__content {
    padding-left: 22px;
    color: var(--black, #333);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &__link {
    color: var(--primary, #A65CF0);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-decoration-line: underline;
  }
}
