.breadcrumbs {
  display: flex;
  align-items: center;


  &__item {
    position: relative;

    &:not(:last-child) {
      margin-right: 22px;

      &::after {
        position: absolute;
        content: "/";
        right: -14px;
        top: 0;
        color: var(--grey, #999);
      }
    }
  }

  &__link {
    color: var(--grey, #999);
    font-size: 14px;
    line-height: 100%;
    transition: color .3s ease-in-out,  background-color .3s ease-in-out;


    &:not([href]) {
      color: var(--primary-shade, #7033AC);
    }

    &--ef {
      &:focus {
        background-color: var(--pink-violet, #EBD1FF);
        color: var(--primary-shade, #7033AC);
        }

        @include hover {
        color: var(--primary, #A65CF0);
        background-color: transparent;
        }

        &:active {
        color: var(--primary-shade, #7033AC);
        }
    }


  }
}








