.tooltip {

  &--ic {
    fill: #FF862F;
    border-radius: 100px;
    transition: fill .3s ease-in-out;
  }

    &__btn {
      position: relative;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      width: 14px;
      height: 14px;
      border-radius: 100px;


      svg {
        position: absolute;
        width: 14px;
        height: 14px;
        object-fit: cover;
      }

      &:focus {
        + .tooltip__txt {
          opacity: 1;
          visibility: visible;
        }
      }

      @include hover {
        + .tooltip__txt {
          opacity: 1;
          visibility: visible;
        }

        .tooltip-ic {
          fill: #fff;
        }

        background-color: #FF862F;
      }
    }

    &__txt {
      max-width: 157px;
      bottom: 11px !important;
      padding: 5px;
      color: var(--white, #FFF);
      text-align: center;
      font-size: 9px;
      line-height: 14px;
      background: var(--black, #333);
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
    }
  }
