.catalog {
  padding-bottom: 134px;

  &__breadcrumbs {
    margin-bottom: 32px;
  }

  &__content {
    display: flex;
    gap: 32px;
  }

  &__filters {
    width: calc((100% - 96px) / 4);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__wrapper {

  }

  &__subtitle {
    margin-bottom: 20px;
    color: var(--black, #333);
    font-size: 18px;
    font-weight: 400;
    line-height: 130%;
  }

  &__title {
    margin-bottom: 24px;
    color: var(--black, #333);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
  }

  &__filter {
    margin-bottom: 26px;
  }

  &__wrap {
    position: relative;
    width: calc((((100% - 96px) / 4) * 3) + 64px);

  }

  &__cards {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    gap: 32px;
  }
}





.swiper-pagination.catalog__pagination {
  bottom: -32px;
}

.catalog__pagination-bullet {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  text-align: center;
  color: var(--primary, #A65CF0);
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  border-radius: 10px;
  border: 2px solid var(--primary, #A65CF0);
  background-color: #fff;
  opacity: 1;
  transition: color .3s ease-in-out, background-color .3s ease-in-out, border .3s ease-in-out;

  &:hover {
    background-color: var(--primary, #A65CF0);
    color: #fff;
  }

  &:active {
    background-color: var(--primary-shade, #7033AC);
    border-color: var(--primary-shade, #7033AC);
    color: #fff;
  }
}

.catalog__pagination-bullet.swiper-pagination-bullet-active {
  color: #fff;
  border: 2px solid var(--primary, #A65CF0);
  background-color: var(--primary-light, #C998FB);
}


.filter {
  display: flex;
  flex-direction: column;
  gap: 12px;

  &__title {
    margin-bottom: 8px;
    color: var(--grey-shade, #666);
    font-weight: 600;
    line-height: 130%;
  }
}

.title-price {
  margin-bottom: 8px;
  color: var(--grey-shade, #666);
  font-weight: 600;
  line-height: 130%;
}

.catalog-filter {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;

  &__title {
    margin-right: 9px;
    color: var(--grey-shade, #666);
  }

  &__input {
    padding: 4px 13px;
    width: 86px;
    height: 30px;
    border-radius: 2px;
    border: 1px solid var(--grey, #999);
    color: var(--grey-shade, #666);
    line-height: 130%;
    transition: background-color .3s ease-in-out, border .3s ease-in-out, outline .3s ease-in-out;

    &:focus-visible {
      outline: none;
    }

    &:focus {
      background-color: var(--pink-violet, #EBD1FF);
      outline: 1px solid var(--primary, #A65CF0);
    }

    @include hover {
      border: 1px solid var(--primary, #A65CF0);
      border: 1px solid var(--primary, #A65CF0);
    }

    &:active {
      border: 1px solid var(--primary-shade, #7033AC);
    }
  }


  &-element {
      border-radius: 3px;
  background: var(--md-grey, #EAEAEA);
  height: 3px;
  border: none;
  box-shadow: none;
  }


}

.noUi-connect {
  border-radius: 3px;
  background: var(--primary, #A65CF0);
}

.noUi-handle:after, .noUi-handle:before {
display: none;
}

.noUi-horizontal .noUi-handle {
  width: 0;
  height: 0;
  border-left: 6.5px solid transparent;
  border-right: 6.5px solid transparent;
  border-bottom: 13px solid var(--primary, #A65CF0);
  right: -6px;
  top: 0;
  transition: border-bottom .3s ease-in-out;

  &:focus {
    border-bottom: 13px solid var(--primary-shade, #7033AC);
  }

  @include hover {
    border-bottom: 13px solid var(--primary-light, #C998FB);
  }

  &:active {
    border-bottom: 13px solid var(--primary-shade, #7033AC);
  }
}

.noUi-handle {
  border: none;
  // border-radius: 100%;
  background: transparent;
  cursor: default;
  box-shadow: none;
}
