.similar {
padding-top: 82px;
padding-bottom: var( --padding-section);

  &__container {
    position: relative;
  }

  &__title {
    margin-bottom: 24px;
    color: var(--black, #333);
font-size: 24px;
font-weight: 400;
line-height: 130%;
  }

  &__btns  {
        position: absolute;
    top: 0;
    right: 12px;
    display: flex;
    gap: 13px;
  }

  &__card {
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 16px;
    min-height: 360px;
  }

  &__info {
    padding-left: 30px;

  }


}
