.categories {
    padding-top: var(--padding-section);
    padding-bottom: var(--padding-section);

    @include desktop {
      padding-bottom: 75px;
    }

&__card {
  padding: 28px 30px 28px 30px;
  @include desktop {
    min-height: 507px;
  }


  @include tablet {
    padding: 25px 30px 53px 30px;
  }

  img {
    @include desktop {
      min-height: 296px;
    }

  }


  &--big {
    grid-column: 6 span;
  }

  &--big2 {
    @include desktop {
      grid-row-start: 2;
      grid-column: 4 / 7;

      img {
        max-height: 202px;
        max-width: 258px;
        object-fit: revert;
      }
    }
  }

  &--small {
    grid-column: 4 span;

    @include desktop {
      grid-column: 3 span;
    }
  }

  .card__link {
    @include desktop {
      position: absolute;
      bottom: 28px;
      left: 30px;
    }
  }
}

&__list {
  margin-bottom: 12px;
}

&__title {
  margin-bottom: 30px;

  // @include tablet {
  //   margin-bottom: 36px;
  // }
}

&__image {
  margin-left: -30px;
  margin-bottom: 30px;
}
}
