.header {
  position: relative;
  margin-bottom: 32px;

  &::before {
    position: absolute;
    z-index: -1;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    background-color: var(--grey-light, #F6F6F6);

    @include desktop {
    top: 57px;
    }

    @include mobile {
      top: 0;
      }

  }



  &__container {
    grid-template-rows: 51px 40px 60px;
    gap: 25px 32px;

    @include desktop {
      grid-template-rows: 57px 44px 96px 60px;
      gap: 2px 32px;
    }

    @include mobile {
      grid-template-rows: 57px 44px 23px 76px;
    }
  }

  &__region {
    grid-column: 2 span;
    align-items: center;
    gap: 4px;

    @include mobile {
      grid-column: 4 span;
    }


    .choices__list--dropdown .choices__item--selectable::after,
    .choices__list[aria-expanded] .choices__item--selectable::after {
      display: none;
    }

    .choices {
      width: 80px;
      height: 20px;
      color: var(--primary, #A65CF0);
      transition: color .3s ease-in-out, background-color .3s ease-in-out;

      &:focus {
        background: var(--pink-violet, #EBD1FF);
      }

      @include hover {
        color: var(--primary-light, #C998FB);
      }

      &:active {
        color: var(--primary-shade, #7033AC);
      }

      &::after {
        content: "";
        height: 8px;
        width: 8px;
        border-style: solid;
        border-color: transparent currentColor currentColor transparent;
        border-width: 1px;
        position: absolute;
        right: 6.5px;
        top: 50%;
        margin-top: -5.5px;
        pointer-events: none;
        transform: rotate(45deg);
      }
    }

    .choices[data-type*=select-one].is-open::after {
      border-color: currentColor transparent transparent currentColor;
      margin-top: -2.5px;
    }

    .choices__inner {
      background-color: transparent;
      padding: 0;
      border: none;
      font-size: 16px;
      min-height: 20px;
      height: 20px;
      color: var(--primary, #A65CF0);
      transition: color .3s ease-in-out, background-color .3s ease-in-out;

      &:focus {
        background-color: var(--pink-violet, #EBD1FF);
      }

      @include hover {
        color: var(--primary-light, #C998FB);
        background-color: #fff;
      }

      &:active {
        color: var(--primary-shade, #7033AC);
      }
    }

    .choices[data-type*=select-one] .choices__inner {
      padding-bottom: 0;
    }

    .choices__list--single {
      padding: 0;
    }

    .choices__list--dropdown .choices__item--selectable,
    .choices__list[aria-expanded] .choices__item--selectable {
      padding-right: 0;
    }

    .choices__list--dropdown,
    .choices__list[aria-expanded] {
      margin-top: 20px;
      border: 2px solid var(--grey-light, #F6F6F6);
    }

    .choices__list--dropdown .choices__item--selectable.is-highlighted,
    .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
      background-color: #fff;
    }

    .choices__list--dropdown .choices__item,
    .choices__list[aria-expanded] .choices__item {
      font-size: 14px;
      color: var(--black, #333);
    }
  }

  &__link {
    grid-column: 2 span;
    transform: translateX(-10px);

    @include desktop {
      grid-column: 8 / 2 span;
      justify-content: flex-end;
    }

    @include tablet {
      grid-column: 5 / 2 span;
    }

    @include mobile {
      grid-column: 1 / 4 span;
      grid-row: 3;
      justify-content: flex-start;
      transform: translateX(0);
    }
  }

  &__nav-additional {
    margin-right: -10px;
    grid-column: 8 span;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include desktop {
      grid-column: 7 span;
      justify-content: flex-start;
    }

    @include tablet {
      grid-column: 6 span;
    }
  }

  &__logo {
    grid-column: 2 span;

    @include tablet {
      grid-column: 3/2 span;
    }

    @include mobile {
      grid-column: 2/4 span;
      grid-row: 1;
      justify-self: center;
    }
  }

  &__nav {
    margin-right: -8px;
    grid-column: 10 span;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @include desktop {
      grid-column: 7 span;
      justify-content: flex-end;
    }

    @include tablet {
      display: none;
    }
  }

  &__search {
    grid-column: 10 span;

    @include desktop {
      grid-column: 9 span;
    }

    @include tablet {
      grid-column: 6 span;
    }

    @include mobile {
      flex-direction: column;
      gap: 16px;
    }

    .choices__list--dropdown .choices__item--selectable::after,
    .choices__list[aria-expanded] .choices__item--selectable::after {
      display: none;
    }

    .choices {
      z-index: 1000;
      max-width: 300px;
      width: 100%;
      height: 60px;
      color: var(--primary, #A65CF0);
      border-radius: 0px 10px 10px 0px;
      border: 2px solid var(--grey-light, #F6F6F6);
      transition: background-color .3s ease-in-out, border-color .3s ease-in-out;

      @include tablet {
        max-width: 189px;
      }

      @include mobile {
        max-width: 100%;
      }


      &:focus {
        border-color: var(--primary, #A65CF0);

        .choices__inner {
          background-color: var(--pink-light, #F8EFFF);
        }
      }

      &:hover {
        border-color: var(--primary, #A65CF0);

        .choices__inner {
          background-color: #fff;
        }
      }

      &:active {
        border-color: var(--primary-shade, #7033AC);
      }

      &::after {
        content: "";
        height: 8px;
        width: 8px;
        border-style: solid;
        border-color: transparent currentColor currentColor transparent;
        border-width: 1px;
        position: absolute;
        right: 32px;
        top: 50%;
        margin-top: -5.5px;
        pointer-events: none;
        transform: rotate(45deg);
      }
    }

    .choices__placeholder {
      opacity: 1;
    }

    .choices[data-type*=select-one].is-open::after {
      border-color: currentColor transparent transparent currentColor;
      margin-top: -2.5px;
    }

    .choices__inner {
      padding: 20px 32px;
      font-size: 16px;
      min-height: 60px;
      height: 60px;
      color: var(--primary, #A65CF0);
      border: none;
      background-color: var(--white, #fff);
    }

    .choices[data-type*=select-one] .choices__inner {
      padding-bottom: 0;
    }

    .choices__list--single {
      padding: 0;
    }

    .choices__list--dropdown .choices__item--selectable,
    .choices__list[aria-expanded] .choices__item--selectable {
      padding-right: 0;
    }

    .choices__list--dropdown,
    .choices__list[aria-expanded] {
      border-radius: 0px 0 10px 10px;
      border: 2px solid var(--grey-light, #F6F6F6);
      background: var(--white, #FFF);
    }

    .choices__list--dropdown .choices__item--selectable.is-highlighted,
    .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
      background-color: #fff;
      color: #A65CF0;
    }

    .choices__list--dropdown .choices__item,
    .choices__list[aria-expanded] .choices__item {
      font-size: 16px;
      color: var(--black, #333);
    }

    .choices__list--dropdown {
      padding: 22px 0 22px 30px;
    }

    .choices__list {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;
    }

    .choices__item {
      width: 100px;
    }


    #choices--category-item-choice-3 {
      display: none;
    }
  }

  &__cabinet {
    margin-left: auto;
    grid-column: 2 span;
    align-items: center;
    gap: 20px;

    @include desktop {
      grid-row-start: 2;
      grid-column: 8/2 span;
    }

    @include tablet {
      grid-row-start: 3;
      grid-column: 5/2 span;
      justify-self: end;
    }

    @include mobile {
      grid-row-start: 4;
      grid-column: 4/3 span;
    }
  }
}


.region {
  &__title {
    color: var(--black, #333);
    line-height: 120%;
  }
}


.choices.is-open {
  background-color: transparent !important;
  border-color: var(--grey-light, #F6F6F6) !important;

  .choices__inner {
    background-color: #fff !important;
  }
}




.wraper-burger {
  grid-column: 1 span;
  grid-row-start: 3;

  display: flex;
  align-items: center;
  justify-content: center;


  @include mobile {
    grid-row-start: 4;
  }
}


.burger {
  display: none;
  position: relative;
  border: none;
  padding: 0;
  width: 24px;
  height: 24px;
  color: #A65CF0;
  background-color: transparent;
  cursor: pointer;

  @include tablet {
    display: block;
  }



  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: currentColor;
    border-radius: 4px;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - 3px);
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 3px;
    background-color: currentColor;
    border-radius: 4px;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}

