.offers {
  padding-bottom: var(--padding-section);

  @include mobile {
    padding-bottom: 0;


  }

  &__container {
    position: relative;
    @include mobile {
      padding-bottom: 118px;
    }
  }

  &__btns {
    position: absolute;
    top: 0;
    right: 12px;
    display: flex;
    gap: 13px;

    @include desktop {
      right: 28px;
    }

    @include tablet {
      right: 64px;
    }

    @include mobile {
      top: initial;
      bottom: 70px;
      right: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 76px;
    }


  }

  &__slide {
    @include middle-desktop {
      width: calc((51.25% - 64px) / 2) !important;

      &:nth-child(3n + 2) {
        width: 48.75% !important;
      }
    }
  }

  &__card {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 16px;
    padding: 34px 30px 26px 30px;
    min-height: 451px;
    // flex-shrink: 0;

  }

  &__discount {
    position: absolute;
    top: 30px;
    left: 0;
  }

  &__image {}

  &__info {
padding: 0;
  }


  &__prices {
    display: flex;
    align-items: center;
    gap: 30px;
  }

  &__bg {
    position: absolute;
    bottom: 30px;
    right: 30px;

    @include desktop {
      display: none;
    }
  }

  &__btn {
    min-width: 106px;
  }
}
