.phone {
  align-self: center;
  justify-self:start;
  display: inline-flex;
  align-items: center;
  color: var(--secondary, #FF862F);
  font-size: 14px;
  line-height: 100%;
  transition: color .3s ease-in-out,  background-color .3s ease-in-out;

  &:focus {
    background-color:  var(--bone, #FFF5D1);
  }

  @include hover {
    color: var(--secondary-light, #FFBD59);
    background-color: transparent;
  }

  &:active {
    color: var(--secondary-shade, #BB370E);
  }

  svg {
    width: 22px;
    height: 22px;
    fill: currentColor;
  }

  @include tablet {
    justify-self: end;
  }

  @include mobile {
    justify-self: start;
  }

}
